import React from "react";
import PropTypes from "prop-types";
import clsx from "clsx";

import { Row, Col } from "react-bootstrap";
import PageSection from "components/PageSection";
import Client from "components/Client";
import SectionHeader from "components/SectionHeader";

import "./Clients.scss";

const Clients = ({ className, frontmatter }) => {
  if (!frontmatter) {
    return null;
  }

  const { anchor, clients, header } = frontmatter;

  return (
    <PageSection className={clsx("noShow", "py-5", className)} id={anchor}>
      <Row>
        <SectionHeader header={header} />
      </Row>
      <Row>
        {clients.map(({ imageFileName }) => (
          <Col className="my-4" key={imageFileName}>
            <Client imageFileName={imageFileName} />
          </Col>
        ))}
      </Row>
    </PageSection>
  );
};

Clients.propTypes = {
  className: PropTypes.string,
  frontmatter: PropTypes.object,
};

Clients.defaultProps = {
  className: null,
  frontmatter: null,
};

export default Clients;
